import {request} from './request'

export function getDetailMultiData() {
  return request({
    //url:'/test/getBanners'
	//url:'/vue/VueProjectData/manager/product.php'
	url:'/vue/VueProjectData/manager/detail.php'
  });
}

export function getDetailsData(id) {
  return request({
	url:'/vue/VueProjectData/manager/detail.php',
    params:{
      id,
    }
  });
}

export class Goods{
	constructor(itemInfo,culumns){
		this.title=itemInfo.title
		this.id=itemInfo.id
		this.desc=itemInfo.desc
		this.newPrice=itemInfo.newPrice
		this.oldPrice=itemInfo.oldPrice
		this.discount=itemInfo.discountDesc
		this.culumns=culumns
		this.price=itemInfo.price
	}
}

export class Shop{
	constructor(itemInfo,detailImages){
		this.name=itemInfo.name
		this.brandlogo=itemInfo.brandLogo
		this.detailImages=detailImages
	}
}

export function getRecomendData(id) {
  return request({
	url:'/vue/VueProjectData/manager/recomenddata.php',
    params:{
      id,
    }
  });
}

export function getGoodsData(type, page) {
  return request({
	url:'/vue/VueProjectData/manager/goods.php',
    params:{
      type,
      page
    }
  });
}



