			oid:role.id,
			level:role.level,
			price:role.price,
			serve:role.serve,
			type:'tohdayOrder',
		}
		//插入到收藏里
		this.$store.dispatch('addRole',roleObj).then(res=>{
			console.log(res)
		})
	}
  },
  created(){
	getHomeMultiData().then(res=>{
		this.roleArr.push(...res)
	})
	
	getDetailsData().then(res=>{
